<template>
  <div>
    <el-dialog
      :visible.sync="modal"
      :title="lang['create-new-lesson']"
      center
      top="5vh"
    >
      <form id="form-lesson-videoconf" @submit.prevent="create()">
        <div class="form-row">
          <!-- Module id -->
          <input type="number" class="hide" name="moduleId" :value="moduleId" />
          <!-- Type lesson -->
          <input type="text" class="hide" name="type_mylesson_id" value="5" />
          <div class="form-group col-xl-12 col-md-12">
            <!-- Lesson name -->
            <label class="col-form-label">{{ lang['name'] }} *</label>
            <el-input required v-model="videoconf.name" name="title"></el-input>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-xl-12 col-md-12">
            <!-- Lesson description -->
            <label class="col-form-label">{{ lang['description'] }} *</label>
            <el-input
              type="textarea"
              name="description"
              rows="3"
              v-model="videoconf.description"
            ></el-input>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-xl-12 col-md-12">
            <!-- Lesson date -->
            <label class="col-form-label">{{ lang['date'] }} *</label>
            <div class="block">
              <el-date-picker
                required
                v-model="videoconf.date"
                type="date"
                name="date"
                format="yyyy/MM/dd"
                value-format="yyyy-MM-dd"
                placeholder="Pick a day"
              ></el-date-picker>
            </div>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-xl-12 col-md-12">
            <!-- Lesson Schedule -->
            <label class="col-form-label">{{ lang['schedule'] }} *</label>
            <div class="block">
              <el-time-select
                required
                v-model="videoconf.schedule"
                name="time"
                :picker-options="{
                  start: '00:00',
                  end: '23:30'
                }"
                placeholder="Select time"
              ></el-time-select>
            </div>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-xl-6 col-md-6">
            <el-button
              class="sbr-primary"
              v-loading="loading"
              native-type="submit"
              >{{ lang['save-button'] }}</el-button
            >
          </div>
        </div>
      </form>
    </el-dialog>
  </div>
</template>

<script>
import { eventBus } from '@/components/newcourse/App';
import { mapState } from 'vuex';

export default {
  props: ['module-id'],
  data: () => {
    return {
      videoconf: {
        name: '',
        url: '',
        date: '',
        description: '',
        schedule: ''
      },
      modal: false,
      loading: false
    };
  },
  mounted() {
    eventBus.$on('new-videoconf', () => {
      this.modal = true;
    });
  },
  computed: {
    ...mapState(['lang'])
  },
  methods: {
    create() {
      this.loading = true;
      const form = document.getElementById('form-lesson-videoconf');
      const formData = new FormData(form);
      const urlToBeUsedInTheRequest = this.$getUrlToMakeRequest(
        'lesson',
        'create'
      );
      this.$request.post(urlToBeUsedInTheRequest, formData).then(
        () => {
          this.$successMessage();
          form.reset();
          eventBus.$emit('new-lesson');
          this.modal = false;
          this.loading = false;
        },
        () => {
          this.$errorMessage();
        }
      );
    }
  }
};
</script>
